import type {QueryBuilderElement} from '@github-ui/query-builder-element'
import {StaticProvider} from '../static-provider'

const featureEnabledValues = [
  {value: 'enabled', description: 'The feature is enabled'},
  {value: 'not-enabled', description: 'The feature is not enabled'},
]

const alertCountValues = [
  {value: 'N', description: 'Specific number of alerts'},
  {value: '>N', description: 'Number of alerts greater than N'},
  {value: '>=N', description: 'Number of alerts greater than or equal to N'},
  // eslint-disable-next-line github/unescaped-html-literal
  {value: '<N', description: 'Number of alerts less than N'},
  {value: '<=N', description: 'Number of alerts less than or equal to N'},
]

export class AlertCountProvider extends StaticProvider {
  constructor(
    queryBuilder: QueryBuilderElement,
    name: string,
    singularItemName: string,
    negated: boolean,
    priority: number,
  ) {
    super({
      queryBuilder,
      name,
      suggestions: alertCountValues.concat(featureEnabledValues),
      value: singularItemName,
      description: `${negated ? 'Exclude' : 'Include'} by ${name} enablement state or number of alerts`,
      priority,
    })
  }
}
