import type {QueryBuilderElement} from '@github-ui/query-builder-element'
import {StaticProvider} from '../static-provider'

const featureEnabledValues = (showEligible: boolean) => {
  if (showEligible) {
    return [
      {value: 'enabled', description: 'Feature is enabled'},
      {value: 'eligible', description: 'Feature is eligible to be enabled'},
      {value: 'not-eligible', description: 'Feature is not eligible to be enabled'},
    ]
  } else {
    return [
      {value: 'enabled', description: 'Feature is enabled'},
      {value: 'not-enabled', description: 'Feature is not enabled'},
    ]
  }
}

export class EnablementProvider extends StaticProvider {
  constructor(
    queryBuilder: QueryBuilderElement,
    name: string,
    singularItemName: string,
    priority: number,
    showEligible = false,
  ) {
    super({
      queryBuilder,
      name,
      suggestions: featureEnabledValues(showEligible),
      value: singularItemName,
      description: `Filter by ${name} enablement`,
      priority,
    })
  }
}
