import type {QueryBuilderElement} from '@github-ui/query-builder-element'
import {DynamicProvider} from '../dynamic-provider'

export class CustomPropertyProvider extends DynamicProvider {
  constructor(
    queryBuilder: QueryBuilderElement,
    customProperty: string,
    baseUrl: string,
    negated: boolean,
    priority: number,
  ) {
    const value = `props.${customProperty}`

    super({
      queryBuilder,
      name: customProperty,
      value: negated ? `-${value}` : value,
      description: `${negated ? 'Exclude' : 'Include'} by ${customProperty} property`,
      url: `${baseUrl}&name=${customProperty}`,
      priority,
    })
  }
}

export function generateProviderForCustomProperties(
  queryBuilder: QueryBuilderElement,
  customPropertiesAttribute: string,
  customPropertiesUrlAttribute: string,
  startingPriority: number,
): void {
  const customProperties = JSON.parse(queryBuilder.getAttribute(customPropertiesAttribute) as string)
  const baseUrl = queryBuilder.getAttribute(customPropertiesUrlAttribute) as string
  if (!customProperties || !baseUrl) {
    return
  }
  for (const customProperty of customProperties) {
    new CustomPropertyProvider(queryBuilder, customProperty.name, baseUrl, false, startingPriority)
    new CustomPropertyProvider(queryBuilder, customProperty.name, baseUrl, true, startingPriority)
    startingPriority++
  }
}
