import type {QueryEvent} from '@github-ui/query-builder-element'
import {QueryBuilderElement} from '@github-ui/query-builder-element'
import type {BaseProviderProps} from './base-provider'
import {BaseProvider} from './base-provider'
import type Suggestion from './suggestion'
import {hasMatch} from 'fzy.js'

type StaticProviderProps = BaseProviderProps & {
  suggestions: Suggestion[]
}

function filterSuggestions(suggestions: Suggestion[], query: string): Suggestion[] {
  return suggestions.filter(suggestion => {
    return (query && hasMatch(query, suggestion.value)) || query === ''
  })
}

export class StaticProvider extends BaseProvider {
  suggestions: Suggestion[]

  constructor({queryBuilder, name, value, description, priority, suggestions}: StaticProviderProps) {
    super({
      queryBuilder,
      name,
      value,
      description,
      priority,
    })

    this.suggestions = suggestions
  }

  override handleEvent(event: QueryEvent): void {
    this.showNameOrSuggestions(event, (selectedValues: string[], currentValue: string) => {
      const suggestions = this.suggestions.filter(value => !selectedValues.includes(value.value))
      super.emitData(filterSuggestions(suggestions, currentValue))
    })
  }
}
