import type {QueryBuilderElement} from '@github-ui/query-builder-element'
import {
  TeamProvider,
  TopicProvider,
  AlertCountProvider,
  ArchivedProvider,
  HasSeverityProvider,
  RepositoryProvider,
  RepositoryVisibilityProvider,
  OwnerProvider,
  OwnerTypeProvider,
} from './providers'
import {StaticProvider} from './static-provider'
import focusOnSearchInputAndAnnounceResults from './utils/focus-on-search-input-and-announce-results'
import {generateProviderForCustomProperties} from './providers/custom-property-provider'

const ID = 'security-center-risk-search'
const TARGET_ID = `query-builder-${ID}`
const sortValues = [
  {value: 'last-updated', description: 'Last updated'},
  {value: 'repos', description: 'Repository name'},
  {value: 'dependabot', description: 'Dependabot alerts'},
  {value: 'code-scanning', description: 'Code scanning alerts'},
  {value: 'secret-scanning', description: 'Secret scanning alerts'},
]

class SortProvider extends StaticProvider {
  constructor(queryBuilder: QueryBuilderElement, priority: number) {
    super({
      queryBuilder,
      name: 'Sort',
      value: 'sort',
      description: '',
      priority,
      suggestions: sortValues,
    })

    this.queryBuilder.addEventListener('query', this)
    this.queryBuilder.attachProvider(this)
  }
}

document.addEventListener('query-builder:request-provider', (event: Event) => {
  const eventTarget: QueryBuilderElement | null = event.target as QueryBuilderElement
  if (!eventTarget || eventTarget.id !== TARGET_ID) return

  new ArchivedProvider(eventTarget, 1)
  new AlertCountProvider(eventTarget, 'code scanning', 'code-scanning-alerts', false, 2)
  new AlertCountProvider(eventTarget, 'code scanning', 'code-scanning-alerts', true, 2)
  new AlertCountProvider(eventTarget, 'Dependabot', 'dependabot-alerts', false, 3)
  new AlertCountProvider(eventTarget, 'Dependabot', 'dependabot-alerts', true, 3)
  new RepositoryVisibilityProvider(eventTarget, false, 4)
  new RepositoryVisibilityProvider(eventTarget, true, 4)
  new RepositoryProvider(eventTarget, 'data-suggestable-repo-path', false, 5)
  new RepositoryProvider(eventTarget, 'data-suggestable-repo-path', true, 5)
  new AlertCountProvider(eventTarget, 'secret scanning', 'secret-scanning-alerts', false, 8)
  new AlertCountProvider(eventTarget, 'secret scanning', 'secret-scanning-alerts', true, 8)
  new HasSeverityProvider(eventTarget, false, 9)
  new HasSeverityProvider(eventTarget, true, 9)
  new SortProvider(eventTarget, 10)
  new TeamProvider(eventTarget, 'data-suggestable-team-path', false, 11)
  new TeamProvider(eventTarget, 'data-suggestable-team-path', true, 11)
  new TopicProvider(eventTarget, 'data-suggestable-topic-path', false, 12)
  new TopicProvider(eventTarget, 'data-suggestable-topic-path', true, 12)

  // Biz only providers
  if (eventTarget.getAttribute('data-show-biz-only-providers') === 'true') {
    new OwnerProvider(eventTarget, 'data-suggestable-owner-path', false, 6)
    new OwnerProvider(eventTarget, 'data-suggestable-owner-path', true, 6)
    new OwnerTypeProvider(eventTarget, false, 7)
    new OwnerTypeProvider(eventTarget, true, 7)
  } else {
    // Custom properties should be after all other providers.
    generateProviderForCustomProperties(
      eventTarget,
      'data-custom-properties',
      'data-suggestable-custom-property-path',
      13,
    )
  }

  eventTarget.detachElementDefinitionReadyForProviders()
})

// A race condition exists between registering the above event listener and QueryBuilderElement's initialization.
//
// To ensure the event listener is registered prior to the emission of the "query-builder:request-provider" event,
// we must manually emit the event after the event listener is registered to let the QB element know we are ready
// in case we missed the initial `request-provider` event.
document.dispatchEvent(new CustomEvent('query-builder:ready-to-request-provider', {detail: {id: ID}}))

document.addEventListener('turbo:load', () => {
  focusOnSearchInputAndAnnounceResults(ID)
})
