import type {
  FilterProvider,
  QueryEvent,
  QueryFilterElement,
  QueryBuilderElement,
} from '@github-ui/query-builder-element'
import {FilterItem} from '@github-ui/query-builder-element'
import {hasMatch} from 'fzy.js'
import type Suggestion from './suggestion'

export type BaseProviderProps = {
  queryBuilder: QueryBuilderElement
  name: string
  value: string
  description: string
  priority: number
}

export abstract class BaseProvider extends EventTarget implements FilterProvider {
  type = 'filter' as const
  protected queryBuilder: QueryBuilderElement
  readonly name: string
  readonly value: string
  readonly singularItemName: string
  readonly description: string
  readonly priority: number

  constructor({name = '', value = '', description = '', priority = 0, queryBuilder}: BaseProviderProps) {
    super()

    this.queryBuilder = queryBuilder

    this.name = name
    this.value = value
    this.singularItemName = value.charAt(0) === '-' ? value.slice(1) : value
    this.description = description
    this.priority = priority

    this.queryBuilder.attachProvider(this)
    this.queryBuilder.addEventListener('query', this)
  }

  abstract handleEvent(event: QueryEvent): void

  showNameOrSuggestions(event: QueryEvent, emit: (selectedValues: string[], currentValue: string) => void): void {
    const lastPart = event.parsedQuery.at(-1)
    const selectedValues = lastPart?.value.split(',') || []
    const currentValue = selectedValues.pop() || ''
    const lastPartType = lastPart?.type
    const lastPartFilter = (lastPart as QueryFilterElement)?.filter || ''

    // Show this provider in the list if suggestions are not being displayed.
    if (lastPartType !== 'filter' && (hasMatch(currentValue, this.value) || currentValue === '')) {
      this.dispatchEvent(new Event('show'))
      return
    }

    // Do not show this provider's suggestions if this provider was not selected.
    if (lastPartType !== 'filter' || lastPartFilter !== this.value) return

    // Show this provider's suggestions.
    emit(selectedValues, currentValue)
  }

  emitData(values: Suggestion[]): void {
    for (const value of values) {
      const filter = {filter: this.value, value: value.value, name: value.displayName, description: value.description}
      const filterItem = new FilterItem(filter)
      this.dispatchEvent(filterItem)
    }
  }
}
