import {debounce} from '@github/mini-throttle'
import type {QueryEvent} from '@github-ui/query-builder-element'
import {FetchDataEvent} from '@github-ui/query-builder-element'
import type {BaseProviderProps} from './base-provider'
import {BaseProvider} from './base-provider'
import type Suggestion from './suggestion'

type DynamicProviderProps = BaseProviderProps & {
  urlAttribute?: string
  url?: string
}

export const DEBOUNCE_TIME = 500

export class DynamicProvider extends BaseProvider {
  readonly #urlAttribute: string
  readonly #url: string
  #suggestionCache: Map<string, Suggestion[]>
  #abortController: AbortController

  constructor({queryBuilder, name, value, description, priority, urlAttribute, url}: DynamicProviderProps) {
    super({
      queryBuilder,
      name,
      value,
      description,
      priority,
    })

    if (urlAttribute) {
      this.#urlAttribute = urlAttribute
    } else if (url) {
      this.#url = url
    } else {
      throw new Error('DynamicProvider requires a urlAttribute or url to be set.')
    }
    this.#suggestionCache = new Map()
    this.#abortController = new AbortController()
  }

  debouncedFetchData = debounce((selectedValues: string[], currentValue: string): void => {
    this.dispatchEvent(new FetchDataEvent(this.fetchAndEmitSuggestions(selectedValues, currentValue)))
  }, DEBOUNCE_TIME)

  override handleEvent(event: QueryEvent): void {
    this.abortPreviousRequests()
    this.showNameOrSuggestions(event, this.debouncedFetchData)
  }

  abortPreviousRequests() {
    this.#abortController.abort()
    this.#abortController = new AbortController()
  }

  async fetchSuggestions(selectedValues: string[], currentValue: string): Promise<Suggestion[]> {
    const cachekey = this.cacheKey(selectedValues, currentValue)

    if (this.#suggestionCache.has(cachekey)) {
      const suggestionsBefore = this.#suggestionCache.get(cachekey) ?? []
      const suggestionsAfter = suggestionsBefore.filter(value => !selectedValues.includes(value.value))

      // If the user has selected any suggestions from our cached items (matching this query),
      // refresh the cache entry (don't return here) so they'll always see a full list.
      if (suggestionsBefore.length - suggestionsAfter.length < 1) {
        return suggestionsAfter
      }
    }

    const url = this.#urlAttribute ? (this.queryBuilder.getAttribute(this.#urlAttribute) as string) : this.#url
    const suggestionEndpoint = new URL(url, window.location.origin)

    const params = suggestionEndpoint.searchParams
    params.set('selected_values', selectedValues.toString())
    params.set('value', currentValue)
    suggestionEndpoint.search = params.toString()

    try {
      const response = await fetch(suggestionEndpoint.toString(), {
        mode: 'same-origin',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
        signal: this.#abortController.signal,
      })

      const suggestions = (await response.json()) as Suggestion[]
      this.#suggestionCache.set(cachekey, suggestions)

      return suggestions
    } catch (error) {
      // @ts-expect-error catch blocks are bound to `unknown` so we need to validate the type before using it
      if (error.name !== 'AbortError') {
        throw error
      }
    }

    return []
  }

  async fetchAndEmitSuggestions(selectedValues: string[], currentValue: string): Promise<void> {
    this.emitData(await this.fetchSuggestions(selectedValues, currentValue))
  }

  cacheKey(selectedValues: string[], currentValue: string): string {
    return [...selectedValues, currentValue].join(',')
  }
}
