import type {QueryBuilderElement} from '@github-ui/query-builder-element'
import {StaticProvider} from '../static-provider'

const archivedValues = [
  {value: 'true', description: 'Show Archived Repos'},
  {value: 'false', description: 'Show Active Repos'},
]

export class ArchivedProvider extends StaticProvider {
  constructor(queryBuilder: QueryBuilderElement, priority: number) {
    const singularItemName = 'archived'

    super({
      queryBuilder,
      name: 'Archived',
      description: 'Filter by archived repositories',
      suggestions: archivedValues,
      value: singularItemName,
      priority,
    })
  }
}
