import type {QueryBuilderElement} from '@github-ui/query-builder-element'
import {StaticProvider} from '../static-provider'

const severities = [
  {value: 'critical'},
  {value: 'high'},
  {value: 'medium'},
  {value: 'moderate'},
  {value: 'low'},
  {value: 'informational'},
]

export class HasSeverityProvider extends StaticProvider {
  constructor(queryBuilder: QueryBuilderElement, negated: boolean, priority: number) {
    const value = 'has-severity'

    super({
      queryBuilder,
      name: 'Severity',
      suggestions: severities,
      value: negated ? `-${value}` : value,
      description: `${negated ? 'Exclude' : 'Include'} if repo has an alert with a particular severity`,
      priority,
    })
  }
}
