import type {QueryBuilderElement} from '@github-ui/query-builder-element'
import {StaticProvider} from '../static-provider'

const repoVisibilityValues = [{value: 'public'}, {value: 'private'}, {value: 'internal'}]

export class RepositoryVisibilityProvider extends StaticProvider {
  constructor(queryBuilder: QueryBuilderElement, negated: boolean, priority: number) {
    const value = 'is'

    super({
      queryBuilder,
      name: 'Repository Visibility',
      suggestions: repoVisibilityValues,
      value: negated ? `-${value}` : value,
      description: `${negated ? 'Exclude' : 'Include'} by repository visibility`,
      priority,
    })
  }
}
