// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'

type TabCountsResponse = {
  data: {
    dependabotAlerts?: {openCount: number}
    codeScanning?: {openCount: number}
    secretScanning?: {openCount: number}
  }
}

function setTabCount(el: HTMLElement, count: number) {
  if (count > 0) {
    el.textContent = formatCount(count)
    el.setAttribute('aria-label', `${count} alerts`)
  } else {
    el.remove()
  }
}

function formatCount(count: number): string {
  if (count > 999) {
    // Truncate display to something like "4.2k"
    return `${(count / 1000).toFixed(1)}k`
  }

  return count.toLocaleString()
}

async function loadTabCounts(container: HTMLElement) {
  const url = new URL(container.getAttribute('data-url')!, window.location.origin)
  const response = await fetch(url.toString(), {
    headers: {
      Accept: 'application/json',
      'GitHub-Verified-Fetch': 'true',
      'X-Requested-With': 'XMLHttpRequest',
    },
  })
  if (!response.ok) {
    return
  }

  const result: TabCountsResponse = await response.json()
  const counts = result.data

  const dependabotAlertsCountEl = container.querySelector<HTMLElement>('.js-dependabot-alerts-count')
  if (dependabotAlertsCountEl && counts.dependabotAlerts) {
    setTabCount(dependabotAlertsCountEl, counts.dependabotAlerts.openCount)
  }

  const codeScanningCountEl = container.querySelector<HTMLElement>('.js-code-scanning-count')
  if (codeScanningCountEl && counts.codeScanning) {
    setTabCount(codeScanningCountEl, counts.codeScanning.openCount)
  }

  const secretScanningCountEl = container.querySelector<HTMLElement>('.js-secret-scanning-count')
  if (secretScanningCountEl && counts.secretScanning) {
    setTabCount(secretScanningCountEl, counts.secretScanning.openCount)
  }
}

observe('.js-security-center-tab-count-container', function (container) {
  loadTabCounts(container as HTMLElement)
})
