import type {QueryBuilderElement} from '@github-ui/query-builder-element'
import {StaticProvider} from '../static-provider'

const ownerTypes = [
  {value: 'user', description: 'Show repositories owned by users.'},
  {value: 'organization', description: 'Show repositories owned by organizations.'},
]

export class OwnerTypeProvider extends StaticProvider {
  constructor(queryBuilder: QueryBuilderElement, negated: boolean, priority: number) {
    const value = 'owner-type'

    super({
      queryBuilder,
      name: 'Owner Type',
      suggestions: ownerTypes,
      value: negated ? `-${value}` : value,
      description: `${negated ? 'Exclude' : 'Include'} repositories by owner type`,
      priority,
    })
  }
}
