import type {QueryBuilderElement} from '@github-ui/query-builder-element'
import {DynamicProvider} from '../dynamic-provider'

export class RepositoryProvider extends DynamicProvider {
  constructor(queryBuilder: QueryBuilderElement, urlAttribute: string, negated: boolean, priority: number) {
    const value = 'repo'

    super({
      queryBuilder,
      name: 'Repository',
      value: negated ? `-${value}` : value,
      description: `${negated ? 'Exclude' : 'Include'} by repository name`,
      urlAttribute,
      priority,
    })
  }
}
