import type {QueryBuilderElement} from '@github-ui/query-builder-element'
import {
  ArchivedProvider,
  EnablementProvider,
  TeamProvider,
  TopicProvider,
  RepositoryProvider,
  RepositoryVisibilityProvider,
  OwnerProvider,
  OwnerTypeProvider,
} from './providers'
import {StaticProvider} from './static-provider'
import focusOnSearchInputAndAnnounceResults from './utils/focus-on-search-input-and-announce-results'
import {generateProviderForCustomProperties} from './providers/custom-property-provider'

const ID = 'security-center-coverage-search'
const TARGET_ID = `query-builder-${ID}`
const sortValues = [
  {value: 'last-updated', description: 'Last updated'},
  {value: 'repos', description: 'Repository name'},
]

class SortProvider extends StaticProvider {
  constructor(queryBuilder: QueryBuilderElement, priority: number) {
    super({
      queryBuilder,
      name: 'Sort',
      value: 'sort',
      description: '',
      priority,
      suggestions: sortValues,
    })

    this.queryBuilder.addEventListener('query', this)
    this.queryBuilder.attachProvider(this)
  }
}

document.addEventListener('query-builder:request-provider', (event: Event) => {
  const eventTarget: QueryBuilderElement | null = event.target as QueryBuilderElement
  if (!eventTarget || eventTarget.id !== TARGET_ID) return

  new EnablementProvider(eventTarget, 'Advanced Security', 'advanced-security', 1)
  new ArchivedProvider(eventTarget, 2)
  new EnablementProvider(eventTarget, 'code scanning alerts', 'code-scanning-alerts', 3)
  new EnablementProvider(eventTarget, 'code scanning default setup', 'code-scanning-default-setup', 4, true)
  new EnablementProvider(eventTarget, 'code scanning pull request alerts', 'code-scanning-pull-request-alerts', 5)
  new EnablementProvider(eventTarget, 'Dependabot alerts', 'dependabot-alerts', 6)
  new EnablementProvider(eventTarget, 'Dependabot security updates', 'dependabot-security-updates', 7)
  new RepositoryVisibilityProvider(eventTarget, false, 8)
  new RepositoryVisibilityProvider(eventTarget, true, 8)
  new RepositoryProvider(eventTarget, 'data-suggestable-repo-path', false, 9)
  new RepositoryProvider(eventTarget, 'data-suggestable-repo-path', true, 9)
  new EnablementProvider(eventTarget, 'secret scanning alerts', 'secret-scanning-alerts', 12)
  new EnablementProvider(eventTarget, 'secret scanning push protection', 'secret-scanning-push-protection', 13)
  new SortProvider(eventTarget, 14)
  new TeamProvider(eventTarget, 'data-suggestable-team-path', false, 15)
  new TeamProvider(eventTarget, 'data-suggestable-team-path', true, 15)
  new TopicProvider(eventTarget, 'data-suggestable-topic-path', false, 16)
  new TopicProvider(eventTarget, 'data-suggestable-topic-path', true, 16)

  // Biz only providers
  if (eventTarget.getAttribute('data-show-biz-only-providers') === 'true') {
    new OwnerProvider(eventTarget, 'data-suggestable-owner-path', false, 10)
    new OwnerProvider(eventTarget, 'data-suggestable-owner-path', true, 10)
    new OwnerTypeProvider(eventTarget, false, 11)
    new OwnerTypeProvider(eventTarget, true, 11)
  } else {
    // Custom properties should be after all other providers.
    generateProviderForCustomProperties(
      eventTarget,
      'data-custom-properties',
      'data-suggestable-custom-property-path',
      17,
    )
  }

  eventTarget.detachElementDefinitionReadyForProviders()
})

// A race condition exists between registering the above event listener and QueryBuilderElement's initialization.
//
// To ensure the event listener is registered prior to the emission of the "query-builder:request-provider" event,
// we must manually emit the event after the event listener is registered to let the QB element know we are ready
// in case we missed the initial `request-provider` event.
document.dispatchEvent(new CustomEvent('query-builder:ready-to-request-provider', {detail: {id: ID}}))

document.addEventListener('turbo:load', () => {
  focusOnSearchInputAndAnnounceResults(ID)
})
